import { withRouter } from 'next/router'
import * as chroma from 'chroma-js'
import * as _ from 'lodash'
import { nouns, adjectives } from '../data/corpora.js'
import getConfig from 'next-server/config'
import Bar from '../parts/Bar'
import {
  scaleRed,
  scaleBlue,
  scaleTextRed,
  scaleTextBlue,
  colorExtend,
} from '../parts/Static'

let { publicRuntimeConfig } = getConfig()
let linkPrefix = publicRuntimeConfig.linkPrefix

let class_labels = ['negative', 'positive']

const Page = withRouter(
  ({
    font_size,
    line_height,
    grem,
    router,
    analyze,
    data,
    info,
    setAnalyze,
    review_sort,
    setReviewSort,
  }) => {
    let info_ids = info.map(o => o.og_id)
    let topic_info = info[info_ids.indexOf(router.query.id)]

    let reviews = data
      .filter(o => o.url === router.query.id)
      .map(r => {
        let new_r = Object.assign({}, r)
        new_r.author =
          _.sample(adjectives)
            .toLowerCase()
            .replace('-', '') +
          '-' +
          _.sample(nouns)
            .toLowerCase()
            .replace('-', '')
        return new_r
      })

    let sorted = reviews.slice()

    let top_sentences = reviews.map(r => {
      let max = Math.max(
        Math.abs(_.min(r.lime_scores)),
        Math.abs(_.max(r.lime_scores))
      )

      function scaleScore(value) {
        if (value < 0) {
          return scaleRed(Math.abs(value) / max)
        } else {
          return scaleBlue(Math.abs(value) / max)
        }
      }

      let sentences = r.lime_tokens.map((t, i) => {
        let score = r.lime_scores[i]
        console.log(colorExtend(scaleScore(score), 1))
        let scaled = Math.abs(r.lime_scores[i]) / max
        return {
          token: t,
          score: score,
          class: r.class,
          scaled_score: scaled,
          background: scaleScore(score),
        }
      })
      let sorted = _.sortBy(sentences, 'score')
      if (r.class === 'pos') sorted = sorted.reverse()
      return sorted[0]
    })

    let top_pos = top_sentences.filter(s => s.class === 'pos')
    let top_neg = top_sentences.filter(s => s.class === 'neg')

    return (
      <div style={{ maxWidth: 700, margin: '0 auto', padding: grem / 2 }}>
        <div style={{ marginBottom: grem }}>
          <div>{topic_info.Title}</div>
          <div style={{ textIndent: grem }}>{topic_info.Plot}</div>
        </div>
        <div style={{ marginBottom: grem }}>
          <div>Positive review highlights</div>
          <div style={{ textIndent: grem }}>
            {top_pos.slice(0, 4).map(s => (
              <span style={{ marginRight: grem / 2 }}>
                <span
                  style={{
                    backgroundImage: `linear-gradient(${s.background}, ${
                      s.background
                    })`,
                    backgroundSize: '4px 7px',
                    backgroundRepeat: 'repeat-x',
                    backgroundPosition: `0 0.85em`,
                    paddingBottom: 2,
                  }}
                >
                  {s.token}
                </span>{' '}
                by grant-custer
              </span>
            ))}
          </div>
        </div>
        <div>
          <div style={{ marginBottom: grem }}>{reviews.length} reviews</div>
          <div>
            {sorted.map((r, i) => {
              let winner_name = r.class
              let winner_index, scaleWinner, scaleLoser
              if (r.class_probabilities[1] > r.class_probabilities[0]) {
                winner_index = 1
                scaleWinner = scaleBlue
                scaleLoser = scaleRed
              } else {
                winner_index = 0
                scaleWinner = scaleRed
                scaleLoser = scaleBlue
              }

              let max = Math.max(
                Math.abs(_.min(r.lime_scores)),
                Math.abs(_.max(r.lime_scores))
              )

              function scaleScore(value) {
                if (value < 0) {
                  return scaleRed(Math.abs(value) / max)
                } else {
                  return scaleBlue(Math.abs(value) / max)
                }
              }

              return (
                <div style={{ marginBottom: grem * 2 }}>
                  <div style={{ marginBottom: (grem / 2) * 0 }}>
                    <div>
                      <div>
                        <div style={{ textIndent: grem }}>
                          {r.author} wrote &middot; {i * 2 + 3} days ago
                        </div>

                        {r.lime_tokens.map((t, i) => {
                          let score = r.lime_scores[i]
                          let background = scaleScore(score)
                          return (
                            <span>
                              {' '}
                              <span
                                style={{
                                  backgroundImage: `linear-gradient(${background}, ${background})`,
                                  backgroundSize: '4px 5px',
                                  backgroundRepeat: 'repeat-x',
                                  backgroundPosition: `0 0.85em`,
                                  paddingBottom: 2,
                                }}
                              >
                                {t}
                              </span>
                            </span>
                          )
                        })}
                      </div>
                    </div>
                  </div>
                  <div
                    style={{
                      background: scaleWinner(
                        r.class_probabilities[winner_index]
                      ),
                      // textIndent: grem,
                    }}
                  >
                    <span>{class_labels[winner_index]} review</span> &middot;{' '}
                    <span style={{}}>
                      {Math.round(r.class_probabilities[winner_index] * 100)}%
                      certainty
                    </span>
                  </div>
                </div>
              )
            })}
          </div>
        </div>
      </div>
    )
  }
)

export default Page
